import React, { useEffect } from "react"
import { connect } from "react-redux"
import { withRouter } from "react-router-dom"

import { Header, Section, Footer, Card } from "../../components"
import { peopleAction } from "../../redux/actions"

const SuuportPeople = props => {
	const { people, peopleBase, currentIDP, current } = props
	useEffect(() => {
		people()
	}, [people])
	useEffect(() => {
		const { pathname } = props.location
		const currents = pathname.split("/").pop()
		currentIDP(currents)
	}, [props.location.pathname, currentIDP, props.location])
	const base = peopleBase.filter(item => item.name === current)
	return (
		<>
			<Header supp={base[0]} />
			{base &&
				base.map((item, id) => (
					<>
						<Section
							key={id}
							title='Описание'
							color='orange'
							type='description'
						>
							{item.desc}
						</Section>
            <Section title='Социальные сети' color='black' type='description'>
              {item.soc.vk !== "Ссылка" && (
                <a key={id} href={item.soc.vk}>
                <Card
                  image='/img/vk.png'
                  alt={item.name}
                  name={item.name}
                  desc={item.desc}
                />
              </a>
              )}
              {item.soc.fb !== "Ссылка" && (
                <a key={id} href={item.soc.vk}>
                <Card
                  image='/img/vk.png'
                  alt={item.name}
                  name={item.name}
                  desc={item.desc}
                />
              </a>
              )}
              {item.soc.ins !== "Ссылка" && (
                <a key={id} href={item.soc.vk}>
                <Card
                  image='/img/vk.png'
                  alt={item.name}
                  name={item.name}
                  desc={item.desc}
                />
              </a>
              )}
              {item.soc.you !== "Ссылка" && (
                <a key={id} href={item.soc.vk}>
                <Card
                  image='/img/vk.png'
                  alt={item.name}
                  name={item.name}
                  desc={item.desc}
                />
              </a>
              )}
              {item.soc.tw !== "Ссылка" && (
                <a key={id} href={item.soc.vk}>
                <Card
                  image='/img/vk.png'
                  alt={item.name}
                  name={item.name}
                  desc={item.desc}
                />
              </a>
              )}
						</Section>
					</>
				))}

			<Footer />
		</>
	)
}

export default withRouter(
	connect(
		({ People }) => ({ peopleBase: People.items, current: People.currentID }),
		{ ...peopleAction },
	)(SuuportPeople),
)
